.Container5 {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  overflow: hidden;
  color: #fff;

  background: 
    -webkit-gradient(
      linear, 
      left top, left bottom, 
      from(rgba(119, 119, 119, 0.6)), 
      to(rgba(119, 119, 119, 0.6)) 
    ),
    url('./../../../../images/parking_spot.jpg');

  background: 
    -webkit-linear-gradient(
      rgba(119, 119, 119, 0.6), 
      rgba(119, 119, 119, 0.6) 
    ),
