  background: 
    -o-linear-gradient(
      rgba(119, 119, 119, 0.6), 
      rgba(119, 119, 119, 0.6) 
    ),
    url('./../../../../images/parking_spot.jpg');

  background: 
    linear-gradient(
      rgba(119, 119, 119, 0.6), 
      rgba(119, 119, 119, 0.6) 
    ),
    url('./../../../../images/parking_spot.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 75vh;
  background-position: 50% 0%;
  text-align: left;
}

