  font-size: 7vh;
  padding: 10px;
  color: #ffffff;
  -ms-flex: 1 1;
      flex: 1 1;
  text-align: center;
}

.container5_content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  color: #ffffff;
  font-size: 3vh;
  padding-left: 10%;
  padding-right: 10%;
}

.container5_content .row {
  /* height: 50%; */
  padding: 1%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.container5_content .row_title {
  width: 100%;
}

.Container5 .img {
  width: 10%;
  height: 10%;
  padding: 0;
  padding-right: 2%;
}


